import AbstractView from '../../framework/view/abstract-view.js';

import { START_DATE_FORMAT, DATE_TIME_EVENT, TIME_EVENT } from '../../const.js';
import { humanizeDate, getTripDuration } from '../../utils/point.js';


const isFavoriteClassName = (isFavorite) => isFavorite ? 'event__favorite-btn--active' : '';

const createPointTemplate = ({ point, offers, destination }) => {
  const { type, basePrice, dateFrom, dateTo, isFavorite } = point;
  const { name } = destination;

  const humanizePointDate = humanizeDate(dateFrom, START_DATE_FORMAT);
  const dateFromEvent = humanizeDate(dateFrom, DATE_TIME_EVENT);
  const dateToEvent = humanizeDate(dateTo, DATE_TIME_EVENT);
  const timeFromEvent = humanizeDate(dateFrom, TIME_EVENT);
  const timeToEvent = humanizeDate(dateTo, TIME_EVENT);

  const tripDuration = getTripDuration(dateFrom, dateTo);

  return (
    `<li class="trip-events__item">
<div class="event">
  <time class="event__date" datetime="${dateFromEvent}">${humanizePointDate}</time>
  <div class="event__type">
    <img class="event__type-icon" width="42" height="42" src="img/icons/${type}.png" alt="Event type icon">
  </div>
  <h3 class="event__title">${type} ${name}</h3>
  <div class="event__schedule">
    <p class="event__time">
      <time class="event__start-time" datetime="${dateFromEvent}T${timeFromEvent}">${timeFromEvent}</time>
      
      <time class="event__end-time" datetime="${dateToEvent}T${timeToEvent}">${timeToEvent}</time>
    </p>
    <p class="event__duration">${tripDuration}</p>
  </div>
  <p class="event__price">
    &nbsp;<span class="event__price-value">
    ${basePrice}</span>
  </p>
  <h4 class="visually-hidden">Offers:</h4>
  <ul class="event__selected-offers">
    ${offers.map((offer) => {
      const checkedOffer = point.offers.includes(offer.id) ? offer.title : '';

      return (
        `${checkedOffer ? `<li class="event__offer">
        <span class="event__offer-title">${offer.title}</span>
        +&nbsp;
        <span class="event__offer-price">${offer.price}</span>
      </li>` : ''}`);
    }).join('')
    }
  </ul>
  <button class="event__favorite-btn ${isFavoriteClassName(isFavorite)}" type="button">
    <span class="visually-hidden">Add to favorite</span>
    <svg class="event__favorite-icon" width="28" height="28" viewBox="0 0 28 28">
      <path d="M14 21l-8.22899 4.3262 1.57159-9.1631L.685209 9.67376 9.8855 8.33688 14 0l4.1145 8.33688 9.2003 1.33688-6.6574 6.48934 1.5716 9.1631L14 21z"></path>
    </svg>
  </button>
  <button class="event__rollup-btn" type="button">
    <span class="visually-hidden">Open event</span>
  </button>
</div>
</li>`);
};


export default class PointView extends AbstractView {
  #point = null;
  #offers = null;
  #destination = null;

  #handleEditClick = null;

  constructor({ point, offers, destination, onEditClick }) {
    super();
    this.#point = point;
    this.#offers = offers;
    this.#destination = destination;

    this.#handleEditClick = onEditClick;

    this.element.querySelector('.event__rollup-btn').addEventListener('click', this.#editClickHandler);
  }

  get template() {
    return createPointTemplate({
      point: this.#point,
      offers: this.#offers,
      destination: this.#destination
    });
  }

  #editClickHandler = (evt) => {
    evt.preventDefault();
    this.#handleEditClick();
  };
}
